import React from 'react'
import PropTypes from 'prop-types'
import { Link } from 'gatsby'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'

import headerCover from '@/images/header-cover.jpeg'
import avatar from '@/images/avatar.jpeg'
import { faBars, faTimes } from '@fortawesome/free-solid-svg-icons'
import {
  BannerInfo,
  Logo,
  MenuButton,
  NavbarContainer,
  PageHeader,
  PageNavbar,
  Nav,
} from './styled'

function Header({ active, open, setOpen }) {
  const title = "EngineLin's Blog"
  const subtitle = `
    Hi, 我是 Engine Lin./n
    跟著我一起專研網頁前端技術、訓練自我成長。
  `
  const navList = [
    { item: '/', name: '文章' },
    // { item: '/about', name: '關於我' },
    // { item: '/contact', name: '合作' },
  ]

  return (
    <PageHeader style={{ backgroundImage: `url(${headerCover})` }}>
      <PageNavbar className={active && 'active'}>
        <div>
          <NavbarContainer active={active}>
            <Logo
              to="/"
              title={title}
              alt={title}
              className={active && 'active'}
            >
              <img src={avatar} alt={title} />
              <h1>{title}</h1>
            </Logo>

            <Nav active={active} className={open && 'open'}>
              <ul>
                {navList.map(({ item, name }) => (
                  <li key={name}>
                    <Link to={item} alt={name} title={name}>
                      {name}
                    </Link>
                  </li>
                ))}
              </ul>
            </Nav>

            <MenuButton
              type="button"
              className={active && 'active'}
              onClick={() => setOpen(!open)}
            >
              <FontAwesomeIcon icon={open ? faTimes : faBars} />
            </MenuButton>
          </NavbarContainer>
        </div>
      </PageNavbar>

      <BannerInfo>
        {subtitle.split('/n').map((t, i) => (
          <h3 key={i}>{t}</h3>
        ))}
      </BannerInfo>
    </PageHeader>
  )
}

Header.propTypes = {
  active: PropTypes.bool,
}

Header.defaultProps = {
  active: false,
}

export default Header
