import styled from 'styled-components'
import { Link } from 'gatsby'
import { theme } from '@/common/theme'

export const CardContainer = styled(Link)`
  display: inline-block;
  position: relative;
  width: 100%;
  background-color: #fff;
  color: black;
  margin-top: 15px;
  text-decoration: unset;

  @media screen and (min-width: ${theme.mediaQueryMobile}) {
    width: 280px;
    margin: 15px;

    box-shadow: 0 0 20px rgba(0, 0, 0, 0.15);
    &:hover {
      box-shadow: 0 0 30px rgba(0, 0, 0, 0.25);
      transition: box-shadow 0.3s;
    }
  }
`

export const Article = styled.article`
  transition: box-shadow 0.3s;
  cursor: pointer;
`

export const Cover = styled.div`
  position: relative;
  overflow: hidden;
  width: 100%;
  height: auto;

  @media screen and (min-width: ${theme.mediaQueryMobile}) {
    height: 160px;
  }

  &::before {
    content: '';
    position: absolute;
    display: block;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    z-index: 1;
    background-color: transparent;
    transition: background 0.3s;
  }

  & > img {
    display: block;
    width: 100%;
  }
`

export const Info = styled.div`
  width: calc(100% - 30px);
  padding: 15px 15px 50px;

  & > h2 {
    font-size: 22px;
    margin: 0 0 20px;
    font-weight: 500;
    line-height: 1.2;
  }

  & > p {
    word-break: break-all;
    font-size: 18px;
    line-height: 1.6;
  }

  @media screen and (min-width: ${theme.mediaQueryMobile}) {
  }
`

export const Tags = styled.div`
  position: absolute;
  bottom: 30px;
  left: 0;

  padding: 5px 15px;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  color: ${theme.mainColor};
  font-size: 16px;

  span {
    margin-left: 2px;
  }

  p {
    font-size: 14px;
    font-weight: 600;
    margin: 0 5px;
  }
`

export const DateTag = styled.div`
  position: absolute;
  bottom: 10px;
  left: 15px;
  font-size: 16px;
  text-align: end;

  &:before {
    content: 'created at';
    margin-right: 5px;
  }
`
