import React from 'react'
import { graphql } from 'gatsby'
import styled from 'styled-components'

import Layout from '@/layouts/HomeLayout/index.jsx'
import SEO from '@/components/seo'
import PostCard from '@/components/PostCard/index.jsx'
import SideBar from '@/components/SideBar'
import { theme } from '@/common/theme'

function Home({ data }) {
  console.log(data)
  const { edges } = data.allMarkdownRemark
  return (
    <Layout>
      <SEO title="Home" />
      <Container>
        <ContentWrapper>
          {edges.map(({ node }) => (
            <PostCard key={node.id} {...node.frontmatter} />
          ))}
        </ContentWrapper>
        <SideBarWrapper>
          <SideBar />
        </SideBarWrapper>
      </Container>
    </Layout>
  )
}

export const query = graphql`
  {
    allMarkdownRemark {
      edges {
        node {
          id
          frontmatter {
            title
            slug
            subtitle
            date
            tags
            hot
            cover {
              childImageSharp {
                fluid {
                  originalImg
                }
              }
            }
          }
        }
      }
    }
  }
`

const { mediaQueryMobile, mediaQueryDesktop } = theme

export const Container = styled.div`
  margin: 0;

  @media screen and (min-width: ${mediaQueryDesktop}) {
    margin: -15px;
    display: flex;
  }
`

export const ContentWrapper = styled.div`
  @media screen and (min-width: ${mediaQueryMobile}) {
    display: flex;
    flex-wrap: wrap;
    flex: 1;
  }
`

export const SideBarWrapper = styled.div`
  width: 100%;

  @media screen and (min-width: ${mediaQueryMobile}) {
    width: 260px;
  }

  @media screen and (min-width: ${mediaQueryDesktop}) {
    width: 300px;
  }
`

export default Home
